<style scoped>
	/* input:-webkit-input-placeholder {
      
         font-size: 12px;
		 font-weight: 300;
		 letter-spacing: 1px;
    } */
	.pay-detail{
		padding:0.3rem;
	}
	.user-info{
		padding:0.2rem;
		border:1px solid #cecdc9;
		border-radius: 5px;
		height: 1.7rem;
		font-size: 14px;
		line-height: 32px;
	}
	.user-info .number{
		font:0.28rem/0.8rem "";
		color: #000;
	}
	.user-info .addr{
		font:0.26rem/0.6rem "";
		color: #5b5b5b;
	}
	.fee-list{
		border:1px solid #cecdc9;
		border-bottom: none;
		border-radius: 5px;
		margin-top: 0.3rem;
	}
	.fee-list dt,.fee-list dd{
		padding: 0 0.2rem;
		border-bottom:1px solid #cecdc9;
		margin: 0;
		line-height:1.2rem;
		height: 1.2rem;
	}
	.fee-list dt p,.fee-list dd p{
		margin:0;
	}
	.fee-list dt .fee-price{
		color: #ff7200;
	}
	.fee-list dd{
		color: #7d7d7d;
	}

	.discount{
		padding: 0;
		background-color: #f6f7f1;
		border:1px solid #cecdc9;
		border-bottom: none;
		list-style: none;
		margin-top: 0.3rem;
	}
	.discount li{
		padding: 0 0.2rem;
		height: 1rem;
		line-height: 1rem;
		border-bottom:1px solid #cecdc9;
	}
	.discount .can-use{
		/*display: inline-block;*/
		width: 1.35rem;
		height: 0.45rem;
		border:1px solid #ff8a05;
		color:#ff8a05;
		font:0.22rem/0.45rem "";
		text-align: center;
		letter-spacing: 2px;
	}
	.coupon{
		color: #ff8a05;
	}

	.payCount{
		padding: 0 0.2rem;
		height: 1rem;
		line-height: 1rem;
		background-color: #f6f7f1;
		color: #ff8a05;
		border:1px solid #cecdc9;
		margin-top: 0.3rem;
	}
	.bigfont{
		font-size: 0.32rem;
		-font-weight: bold;
	}

	/**/
	.upton-list{
		padding: 0.3rem 0.3rem;
	}
	.upton-list .can-use{
		/* font:0.26rem/1rem ""; */
		font-size: 0.3rem;
		height: 0.6rem;
		border-bottom: 1px solid #878787;
		padding: 10px 10px;
	}
	.uptonDetail{
		position: relative;
		background:url("../../assets/image/bg_courtesy_card.png") no-repeat;
		background-size: 100% 2.15rem;
		margin:0.3rem 0;
		height: 2.15rem;
		padding:0 0.3rem 0 0.4rem;
	}
	.icon{
		background:url('../../assets/image/icon_select.png');
		background-size: cover;
		position:absolute;
		width: 0.4rem;
		height: 0.4rem;
		top: 50%;
		margin-top: -0.2rem;
		margin-left: -0.6rem;
	}
	.iconShow{
		background:url('../../assets/image/icon_selectted.png');
		background-size: cover;
		z-index: 5;
	}
	.detail-left{
		padding-top: 0.35rem;
	}
	.detail-right{
		padding-top: 0.6rem;
		text-align: center;
	}	
	.detail-left .upton-name{
		/*font:0.34rem/0.78rem "";*/
		color: #ff8a00;
		font-size: 0.4rem;
		font-weight: 600;
		letter-spacing: 0.08rem;
	}
	.detail-left .validity{
		width: 1.7rem;
		height: 0.4rem;
		line-height: 0.4rem;
		text-align: center;
		border:1px solid #8e5209;
		border-radius: 10px;
		color: #8e5209;
		margin-top: .1rem;
		margin-bottom: 0.2rem;
	}
	.detail-left .text{
		width: 4.9rem;
		font-size: .2rem;
	}

	.detail-right .upton-number{
		font:0.466rem/0.8rem "";
		color: #fff;
		font-size: .5rem;
	}
	
	.detail-right .upton-type{
		font: 0.3rem/0.6rem "";
		color: #fff;
		font-size: 0.28rem;
		letter-spacing: 0.04rem;
		margin-top: .1rem;
	}
	.btn{
		background-color:#ff8a00; 
		color:#fff;
		width: 6.7rem;
		height: 0.75rem;
		line-height: 0.75rem;
		text-align: center;
		border-radius: 10px;
		position: fixed;
		bottom: 0.5rem;
		-left: 0.4rem;
		margin: 0 auto;
	}

	.pay-btn{
		background-color: #ff8a00;
		position: fixed;
		left: 0;
		bottom: 0;
		bottom: 0;
		z-index: 3;
		width: 100%;
		height: 0.9rem;
		text-align: center;
		color: #fff;
		line-height: 0.9rem;
	}

/**/
.invoice{
	border:1px solid #cecdc9;
	padding-top: 0.5rem;
	margin-top: 0.6rem;
	padding-left: 1rem;
}
.invoice .ty-label{
	padding: 0 0.2rem;
}
.form-row{
	padding-bottom: 0.4rem;
}
.t-label{padding: 20px;font-size: 14px;}
.t-input{margin-left: 8px;border: none;font-size: 12px;outline: none;}
.mint-cell-wrapper{
	border:none;
}
.qufapiao{color: red;margin-bottom: .2rem;}
.box-bg {width: 100%;opacity: .5;height: 100%;position: fixed;
	    background-color: #666;top: 0;left: 0;z-index: 100;display: none}
</style>
<template>
	<div>
		<div  class="pay-detail" v-if="selectUpton">
			<div class="box-bg"></div>
		<!--  -->
			<div class="user-info">
				<div class="number">&nbsp;{{verNumber}}</div>
				<div class="addr ov">
					<p class="fl">{{addr}}</p>
					<p class="fr">{{area}} m<sup style="font-size:0.2rem">2</sup></p>
				</div>
			</div>
			
			<!--  -->
			<dl v-for="item in feeList" class="fee-list ">
				<dt class="ov">
					<p class="fee-name fl" >{{item.service_fee_name}}</p>
					<p class="fee-price fr" >{{item.totalFee.toFixed(2)}}</p>
				</dt>
				<dd class="ov" v-for="i in item.fee_detail">
					<p class="detail-date fl">{{i.service_fee_cycle}}</p>
					<p class="detail-price fr">{{i.fee_price}}</p>
				</dd>
			</dl>
			<!--  -->
			<ul class="discount">
				<li class="ov" @click="uptonList" >
					<div class="fl"> <span class="can-use">&nbsp;<strong>{{uptonNumber}}</strong>&nbsp;&nbsp;</span></div>
					<div class="fr">{{uptonAmount}} &gt;</div> 
				</li>
				<li class="ov coupon">
					<span class="fl" ></span>
					<span class="fr bigfont">{{reduceAmt}} </span>
				</li>
			</ul>
			
			<!--  -->
			<div class="payCount">
				<p class="fl"></p>
				<p class="fr bigfont">{{count}}</p>
			</div>
			<!--  v-show="yins"-->
			<form class="invoice" >
				<div class="form-row">
					:&nbsp;&nbsp;
					 <input  type="radio" id="yes" value="yes" v-model="needInvoice">
  					 <label for="yes" class="ty-label"></label>
					 <input type="radio" id="no" value="no" v-model="needInvoice">
  					 <label for="no" class="ty-label"></label>
				</div>
				<div v-if="needInvoice == 'yes'" class="form-row">
					:&nbsp;&nbsp;
					 <input  type="radio" id="person" value="01" v-model="invoice_title_type">
  					 <label for="01" class="ty-label"></label>
					   <!-- show_com_flag   -->
					 <input v-show="show_com_flag!=='0'" type="radio" id="company" value="02" v-model="invoice_title_type">
  					 <label v-show="show_com_flag!=='0' " for="02" class="ty-label"></label>
				</div>
				<div class="form-row" v-if="invoice_title_type == '02'&&needInvoice=='yes' ">
					<!-- <mt-field label="" placeholder="" v-model="invoice_title"></mt-field> -->
					<label class="t-label">: <input type="text" placeholder="" v-model="invoice_title" class="t-input"> </label>
				</div>
				<div class="form-row" v-if="invoice_title_type == '02'&&needInvoice=='yes' ">
					<!-- <mt-field label="" placeholder="" v-model="credit_code"></mt-field> -->
					<label class="t-label">: <input type="text" placeholder="" v-model="credit_code" class="t-input"> </label>
				</div>
				<h4 class="qufapiao" v-show="show_invoice_flag==0"></h4>
				<h4 class="qufapiao" v-show="show_invoice_flag==1">3,</h4>
			</form>
			<!--  -->
			<div style="height:1rem;"></div>
			<div class="pay-btn" @click="wechatPay"></div>
			<!-- <div class="pay-btn" @click="add"></div> -->
		</div>


		<!--  -->
		<div class="upton-list" v-else>
			<!--  -->
			<div class="can-use">
				<p class="fl"></p>
				<p class="fr">{{uptonNumber}}</p>
			</div>
			<!--  -->
			<div class="uptonDetail" v-for=" (item,index) in uptonData" @click="showIcon(index)">
				<i class="icon" :class="{iconShow:item.selected}" ></i>
				<div class="detail-left fl">
					<div class="upton-name">{{item.title}}</div>
					<p class="validity">{{item.leftDayDes}}</p>
					<span class="text"> {{item.useStartDateStr}}{{item.useEndDateStr}}</span>
				</div>
				<div class="detail-right fr">
					<p class="upton-number">{{item.amount}}</p>
					<p class="upton-type"></p>
				</div>
			</div>
			<!--  -->
			<div class="btn" @click="submit"></div>
		</div>

	</div>
</template>
<script>
	let vm;
	import wx from 'weixin-js-sdk';
	export default {
		data(){
			return {
				yins:false,
				invoice_title:'',//
				credit_code:'',//
				invoiceType:'person',//
				invoice_title_type:'01',//
				needInvoice:'yes',//
				uptonAmount:'',
				upronAmountNumber:0,//// 
				uptonData:[],
				selectUpton:true,//
				// routeParams:{
				// 	bills : this.$route.params.bills,//id 
				// 	stmtId:this.$route.params.stmtId,//
				// 	totalPrice:this.$route.params.totalPrice,//
				// 	reduceMode:this.$route.params.reduceMode,//
				// },
				routeParams:{
					billIds : this.$route.query.billIds,//id 
					stmtId:this.$route.query.stmtId,//
					totalPrice:this.$route.query.totalPrice,//
					reduceMode:this.$route.query.reduceMode,//
				},
				// routeParams:{
				// 	billIds : this.getUrlParams('billIds'),//id 
				// 	stmtId:this.getUrlParams('stmtId'),//
				// 	totalPrice:this.getUrlParams('totalPrice'),//
				// 	reduceMode:this.getUrlParams('reduceMode'),//
				// },
				count:0,//
				hasReduce:'0',//
				reduceAmt:'0',//
				verNumber:'',//
				addr:'',//
				area:'',//
				feeList:'',//
				uptonNumber:0,//
				couponId:'',//id
				mianBill:'0',//id
				mianAmt:0.00,
				show_invoice_flag:'0',
				show_com_flag:'0',//
			}
		},
		beforeCreate(){//
			
		},
		created(){

			vm = this;		
			// location.href.reload();
			this.directRightUrl();
			if(vm.routeParams.stmtId == " "){
				vm.routeParams.stmtId = ""
			}
			// console.log('----------')
			// console.log(this.getUrlParams('billIds'))
			// let urlIndex = location.href.split('#')[0];
			// vm.receiveData.wxconfig(vm,wx,['chooseWXPay']);
			//  
			vm.calcReduceAmt()
		},
		watch:{
			needInvoice:function(){
				if(this.needInvoice == 'yes'){
					vm.invoice_title_type='01'
				}else{
					vm.invoice_title_type=''
				}
			}
		},
		mounted(){
			// this.initSession4Test()

			//
	
	  		// console.log(this.$route.query)
	  		// console.log(this.$route.query.billIds);
	  		// console.log(this.routeParams)
			let url = "getBillDetail";
			vm.receiveData.getData(
				vm,
				url,
				'data',
				function(){
					vm.show_com_flag=vm.data.result.show_com_flag;
					vm.show_invoice_flag = vm.data.result.show_invoice_flag
					if(vm.data.result.fee_data  == null){
						
						// var herf1 = "https://www.e-shequ.com/weixin/wuye/index.html";
						// location.href = herf1;
					}
	  				let useDate = vm.data.result.fee_data[0];
	  				let mianBill = vm.data.result.mianBill;
	  				let mianAmt = vm.data.result.mianAmt;
	  				if ( mianBill) {
						vm.mianBill = vm.data.result.mianBill;
					}
					if ( mianAmt) {
						vm.mianAmt = vm.data.result.mianAmt;
					}

	  				//
	  				vm.verNumber = useDate.ver_no;
	  				//
	  				vm.addr = useDate.cell_addr;
					
	  				//
	  				vm.area = useDate.cnst_area;
	  				//
	  				vm.feeList = useDate.fee_name;
	  			},

	  			{
	  				billId :vm.routeParams.billIds,
	  				stmtId :vm.routeParams.stmtId
	  			}
	  		);
	  		//   :
	  		let url2 = 'updateCouponStatus';
	  		vm.receiveData.getData(vm,url2,'temp',function(){	  			
	  		    // 
	  			let url3 = 'getCouponsPayWuYe';
	  			vm.receiveData.getData(vm,url3,'uptonData',function(){
	  				vm.uptonData = vm.uptonData.result;
	  				vm.uptonNumber = vm.uptonData.length;
	  			})
	  		});
	  		// 
		},
		
		methods:{
					 initSession4Test(){
							let url = '/initSession4Test/105';
								vm.receiveData.getData(vm,url,'Data',function(){
							});
          		  },

			// url
			directRightUrl () {
			  let paths = window.location.href.split('#')
			  paths[1] = paths[1] || '/'
			  // #!
			  if (paths[0].charAt(paths[0].length - 1) !== '?') {
			    paths[0] = `${paths[0]}?`
			  }
			  if (paths[1].charAt(0) === '!') {
			     paths[1] = paths[1].substr(1)
			  }
			  let url = `${paths[0]}#${paths[1]}`
			  if (window.location.href !== url) {
			  	console.log(url);
			    window.location.href = url
			  }
			},

			//
			showIcon(index){
				if(vm.uptonData[index].selected){
					vm.$set(vm.uptonData[index],'selected',false);
				}else{
					vm.$set(vm.uptonData[index],'selected',true);
					for(let i in vm.uptonData){
						if(i != index && vm.uptonData[i].selected == true){
							vm.$set(vm.uptonData[i],'selected',false);
							break;							
						}
					}
				}
			},
			

			//
			calcReduceAmt(){
				//
				vm.count = vm.routeParams.totalPrice;//
				let reduced_amt = 0;//
				let reduce_rate = 0;//
				if ("0" == vm.routeParams.reduceMode) {	// 
					return;
				}else if ("1" == vm.routeParams.reduceMode) {	//
					reduce_rate = "1";
					reduced_amt=Math.round(vm.count*reduce_rate)/reduce_rate;
					vm.hasReduce = "1";
				}else if ("2" == vm.routeParams.reduceMode) {	//
					reduce_rate = "10";
					reduced_amt=Math.round(vm.count*reduce_rate)/reduce_rate;
					vm.hasReduce = "1";
				}else {
					return;
				}
				vm.count = reduced_amt.toFixed(2);//
				vm.reduceAmt = parseFloat(vm.routeParams.totalPrice) - parseFloat(vm.count);
				vm.reduceAmt = vm.reduceAmt.toFixed(2);//
			},
			//
			uptonList(){
				if(vm.uptonNumber == 0){//
					return
				}else{
					vm.selectUpton = false;	
				}
				
			},
			submit(){//
				let flag = false;
				for(let i in vm.uptonData){
					if(vm.uptonData[i].selected == true){
						flag = true;
						vm.couponId = vm.uptonData[i].id;//id
						vm.uptonAmount = '' + vm.uptonData[i].amount+'';//
						vm.upronAmountNumber = vm.uptonData[i].amount;
						vm.count = vm.routeParams.totalPrice;//
						vm.count -= vm.uptonData[i].amount
						vm.count = vm.count.toFixed(2);//
						if(vm.count < 0 ){
							vm.count = 0;
						}
					}
					if(!flag){
						vm.uptonAmount = "";
						vm.upronAmountNumber = 0,
						vm.couponId = '';
						vm.calcReduceAmt();
					}
					vm.selectUpton = true;
				};
				
				
			},
			// postpost
			wechatPay(){
				//vm, url, params,backdataname,callback
				//let url = 'getPrePayInfo'
				if(this.needInvoice=="yes"){
					if(this.invoice_title_type=="02"){
						if(this.invoice_title==""){
							alert("");
							return;
						};
						if(this.credit_code==""){
							alert("");
							return;
						}
					}
				};
				$('.box-bg').css("display",'block');
				let url = "getPrePayInfo?billId="+vm.routeParams.billIds+"&stmtId="+vm.routeParams.stmtId+"&couponUnit="+vm.upronAmountNumber+"&couponNum=1&couponId="+vm.couponId+"&mianBill="+vm.mianBill+"&mianAmt="+vm.mianAmt+"&reduceAmt="+vm.reduceAmt+"&invoice_title_type="+this.invoice_title_type+"&credit_code="+this.credit_code+"&invoice_title="+this.invoice_title;
				
				this.axios.post(
					url,
					{},
				).then(
					function(res){
						let wd = JSON.parse(res.data);
						console.log(wd);
						if(wd.success == false){
							alert(wd.message);
							$('.box-bg').css("display",'none');
							return;

						}
						wx.config({
		                    debug: false, // ,apialertpclogpc
		                    appId: wd.result.appid, // 
		                    timestamp: wd.result.timestamp, // 
		                    nonceStr: wd.result.noncestr, // 
		                    signature: wd.result.paysign,// 1
		                    jsApiList: ['chooseWXPay'] // JSJS2
		                });
		                wx.chooseWXPay({
						 	"appId":wd.result.appid,
			                "timestamp":wd.result.timestamp,
			                "nonceStr":wd.result.noncestr,
			                "package":wd.result.packageValue,
			                "signType":wd.result.signtype,
			                "paySign":wd.result.paysign,
							
			          	    success: function (res) {
			          	    	// alert("");
			          	    	// window.location.href = "https://www.baidu.com";
								let reqUrl = "noticePayed?billId="+vm.routeParams.billIds+"&stmtId="+vm.routeParams.stmtId+"&tradeWaterId="+wd.result.trade_water_id+"&packageId="+wd.result.packageId+"&feePrice="+vm.routeParams.totalPrice;
								
								if(vm.uptonAmount != ""){
									// alert("")
									reqUrl += "&couponId="+vm.couponId;
									// alert(reqUrl)
								}
								
								// if()098765z
								vm.receiveData.postData(vm,reqUrl,{},'reqUrlData',function(){
									vm.payInfo = vm.reqUrlData.result;
									// vm.payInfofee_data = vm.reqUrlData.result.fee_data;	  		
					  				// alert("");
					  			var	forwardUrl = vm.config.wuye_zhifu.url1;
					  				window.location.href = forwardUrl;
					  			})											
			          	    },

			          	    fail:function(res) {
			          	    	console.log(JSON.stringify(res))
			          	    },
			          	    cancel:function(res){
			          	    	alert('');
			          	    	$('.box-bg').css("display",'none');
								// console.log(JSON.stringify(n));
								// o.isPaying = false;
						  //       commonui.hideAjaxLoading();
						  //       $("#zzmb").hide();
							}
			          	    
			          	})
					}
				).catch(
					function(err){
						console.log(err);
					}
				)
			},



			notifyPaySuccess (){
				let reqUrl = "noticePayed?billId="+vm.routeParams.billIds+"&stmtId="+vm.routeParams.stmtId;
					vm.receiveData.getData(vm,reqUrl,'reqUrlData',function(){
						vm.payInfo = vm.reqUrlData.result;
						vm.payInfofee_data = vm.reqUrlData.result.fee_data;	  		
		  				//console.log(999)
		  		})
			},
			getUrlParams(){

				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");    //name
				var r = window.location.search.substr(1).match(reg);   // (name) r
				if (r != null) return unescape(r[2]); return null;     //
			},

		}


	}
</script>


