import Vue from 'vue'
import App from './App'
import router from './router'

/*  mint-ui  axios*/
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import $ from 'jquery'

import Qs from 'qs';
import axios from 'axios';
//axios.defaults.withCredentials=true; //cookie
import VueAxios from 'vue-axios';
import './assets/css/base.css'

import './assets/css/normalize.css'
import './assets/js/rem.js'

import cookie from 'js-cookie'
// import common from './assets/js/common.js'
Vue.prototype.common = common;
// import config from './assets/js/config.js'
Vue.prototype.config = config;

import receiveData from './assets/js/receiveData.js'
Vue.prototype.receiveData = receiveData;



Vue.use(MintUI)
//axios 
var axiosInstance = axios.create({
    transformRequest: [function (data) {
        data = JSON.stringify(data);
        return data;
    }],
    headers: {
        'Content-Type':"application/json",
        'Accept': 'application/json',
    },
    timeout: 10000,
    baseURL: Vue.prototype.config.baseUrl,
    withCredentials:true,
    transformResponse: [function (data) {//
      return data;
    }],
});
//axios cookie
axiosInstance.interceptors.request.use(
    config => {
        if( !cookie.get('session')){//seesion   seesion
        }else{// session
          config.headers.Cookie =`${ cookie.get('Cookie') }`
        }
         return config
    },
    err => {
        return Promise.reject(err)
    }
)

Vue.use(VueAxios, axiosInstance);


//
Vue.config.productionTip = false
// debug
Vue.config.debug = true;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



